.monney__list{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    .monney__list__col{
        overflow: hidden;
        width: 40%;
        max-width: 400px;
        padding: 0px 0px;
        font-size: 16px;
        color: #fff;
        .list-item{
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 6px 20px;
            font-weight: 500;
            border-top: 1px solid rgba(228, 228, 228, 0.452);
            margin: 4px 0px;
            p{
                width: 100%;
            }
            button{
                cursor: pointer;
                padding: 3px 12px;
                border: unset;
                font-size: 16px;
                color: white;
                font-weight: 500;
                background: transparent;
                transform: translateX(300%);
            }
            &:hover{
                button{
                    transform: translateX(0%);
                    &:hover{
                        transform: scale(1.2);
                    }
                }
            }
        }
    }
    .monney__list__heading{
        font-weight: 500;
        font-size: 18px;
    }
    .monney__list--income{
        border: 1px solid var(--incomeColor);
        box-shadow: 0px 0px 2px 0px var(--incomeColor), 1px 2px 4px 0px var(--incomeShadowColor2);
        background-color: var(--incomeColor);
        padding: 10px 0px;
        .income__item{
            background-color: var(--incomeColor);
        }
    }
    .monney__list--expenses
    {
        border: 1px solid var(--expenseColor);
        box-shadow: 0px 0px 2px 0px var(--expenseColor), 1px 2px 4px 0px var(--expenseShadowColor2);
        background-color: var(--expenseColor);
        padding: 10px 15px;
    }
}
@media only screen and (max-width : 550px){
    .list-item{
        button{
            transform: translate(0%, 4px) scale(1.2)!important;
        }
    }
    .monney__list{
        flex-wrap: wrap;
        .monney__list__col{
            margin-bottom: 20px;
            width: 100%;
            max-width: unset;
        }
        .money__input{
            font-size: 14px;
            &::placeholder{
                font-size: 12px;
            }
        }
    }
}